



























import { Component, Vue, Prop } from 'vue-property-decorator';
import { MovieType } from '../types/MovieType';

@Component({
  name: 'MoviesCart',
  components: {},
})
export default class MoviesCart extends Vue {
  @Prop() readonly movie!: MovieType;

  private openMovie() {
    this.$router.push(`/movie/${this.movie.id}`);
  }
}
